import "../CSS/Download.css";
import { collection, getDocs, query, where } from "firebase/firestore";
import { db } from "../../../Firebase.js";
import Dropdown from "react-bootstrap/Dropdown";
import "bootstrap/dist/css/bootstrap.min.css";
import { useEffect, useState } from "react";

function Download({ buildList, getBuildList }) {
  useEffect(() => {
    getBuildData();
  }, []);

  // TODO:   
  let [versionList, setVersionList] = useState([]);
  // TODO:  
  let [exData, setExData] = useState("");

  // TODO:  
  const getBuildData = async () => {
    const q = query(collection(db, "Upload"));
    const snapShot = await getDocs(q);
    let buildValue = [];
    snapShot.forEach((doc) => {
      buildValue.push(doc.data());
    });

    // TODO: Set     
    let _versionList = new Set();

    if (buildValue) {
      getBuildList(buildValue);

      buildValue.forEach((_data) => {
        let values = Object.values(_data);

        values.forEach((value) => {
          if (value) {
            _versionList.add(value.version);
          }
        });
      });

      setVersionList(Array.from(_versionList));
    }
  };

  // TODO:   
  let [currentVersion, setCurVersion] = useState("");
  // TODO:      
  let [currentBuildNameList, setBuildNameList] = useState([]);
  // TODO:   
  let [currentBuildName, setCurrentBuildName] = useState("");

  // TODO:     
  useEffect(() => {
    if (buildList) {
      let _list = [];
      buildList.forEach((data) => {
        let values = Object.values(data);

        values.forEach((value) => {
          if (value.version == currentVersion) {
            _list.push(value.buildName);
          }
        });
      });

      setBuildNameList(_list);
      setCurrentBuildName("");
    }
  }, [currentVersion, buildList]);

  const getExData = (_version, _name) => {
    buildList.forEach((data) => {
      let values = Object.values(data);

      values.forEach((value) => {
        if (_version == value.version && _name == value.buildName) {
          setExData(value.explanation);
          setUrl(value.url);
        }
      });
    });
  };

  // TODO:    URL
  let [currentUrl, setUrl] = useState("");

  const OnDownload = () => {
    window.open(currentUrl, "_blank", "noopener", "noreferrer");
  };

  useEffect(() => {
    if (currentVersion) {
      if (currentBuildNameList.length > 0) {
        currentBuildNameList.forEach((data) => {
          if (data == currentBuildName) {
            getExData(currentVersion, currentBuildName);
          }
        });
      }
    }
  }, [currentBuildName]);

  return (
    <>
      <div className="download-body">
        <div>
          <span>Build Version : {currentVersion}</span>
          <Dropdown className="drop">
            <Dropdown.Toggle variant="success" id="dropdown-basic">
              Build Version
            </Dropdown.Toggle>

            <Dropdown.Menu>
              {versionList.map((a, i) => {
                return (
                  <Dropdown.Item
                    key={i}
                    onClick={() => {
                      setCurVersion(a);
                    }}
                  >
                    {a}
                  </Dropdown.Item>
                );
              })}
            </Dropdown.Menu>
          </Dropdown>
        </div>
        <div>
          <span>Build Name : {currentBuildName}</span>
          <Dropdown className="drop">
            <Dropdown.Toggle variant="success" id="dropdown-basic">
              Build Name
            </Dropdown.Toggle>

            <Dropdown.Menu>
              {currentBuildNameList.map((a, i) => {
                return (
                  <Dropdown.Item
                    key={i}
                    onClick={() => {
                      setCurrentBuildName(a);
                    }}
                  >
                    {a}
                  </Dropdown.Item>
                );
              })}
            </Dropdown.Menu>
          </Dropdown>
        </div>
        <div className="ex-body">
          <div className="ex-box">{exData}</div>
        </div>
        <button className="download" onClick={OnDownload}>
          Download
        </button>
      </div>
    </>
  );
}

export default Download;
