import "./App.css";
import Download from "./Component/Download/JS/Download";
import Upload from "./Component/Upload/JS/Upload";
import Progress from "./Component/Progress/JS/Progress";
import { useEffect, useState } from "react";
import { signInWithEmailAndPassword } from "firebase/auth";
import { firebaseAuth } from "./Firebase";
function App() {
  useEffect(() => {
    if (!isAuth) onLogin(0);
  }, []);

  const onLogin = async (index) => {
    await signInWithEmailAndPassword(
      firebaseAuth,
      `edumeta0${index}@edumeta.co.kr`,
      "edumeta007!"
    )
      .then(() => {
        setAuth(true);
      })
      .catch((err) => {
        index += 1;
        switch (err.code) {
          case "auth/email-already-exists": {
            onLogin(index);
            break;
          }
        }
      });
  };

  let [isAuth, setAuth] = useState(false);

  let [isUpload, setUpload] = useState(false);
  let [task, setTask] = useState(0);

  // TODO: isUpload      False    .
  useEffect(() => {
    if (!isUpload) {
      setTask(0);
    }
  }, [isUpload]);

  // TODO:   
  let [buildList, getBuildList] = useState();

  return (
    <div className="main-body">
      {isUpload && <Progress task={task} />}
      <div className="upload-area">
        <div>
          <h1>
            EduMeta Build File <span style={{ color: "#00bfff" }}>Upload</span>
          </h1>
          {isAuth && (
            <Upload
              isUpload={isUpload}
              setUpload={setUpload}
              setTask={setTask}
            />
          )}
        </div>
      </div>
      <div className="download-area">
        <div>
          <h1>
            EduMeta Build File{" "}
            <span style={{ color: "#adff2f" }}>Download</span>
          </h1>
          {isAuth && (
            <Download buildList={buildList} getBuildList={getBuildList} />
          )}
        </div>
      </div>
    </div>
  );
}

export default App;
