import { useEffect, useState } from "react";
import "../CSS/Upload.css";
import { db, storage } from "../../../Firebase.js";
import {
  getDownloadURL,
  ref,
  uploadBytes,
  uploadBytesResumable,
} from "firebase/storage";
import { doc, getDoc, setDoc } from "firebase/firestore";
import uuid from "react-uuid";
import ModalView from "../../Modal/JS/ModalView.js";

function Upload({ isUpload, setUpload, setTask }) {
  let [file, setFile] = useState(null);
  let [buildName, setBuildName] = useState("test");
  let [version, setVersion] = useState("0.0.1");
  let [ex, setEx] = useState(".");
  let [none, setNone] = useState(true);

  // TODO:   id 
  const getUniqueId = () => {
    return uuid();
  };

  // TODO:   
  const getType = (_file) => {
    if (!_file) return;

    let _fileName = `${_file.name}`;
    let lastIndex = _fileName.lastIndexOf(".");
    let fileType = _fileName.slice(lastIndex);
    return fileType;
  };

  // TODO:   GET: URL
  const getFileUrl = async (_ref) => {
    return await getDownloadURL(_ref);
  };

  const uploadBuild = async (_file) => {
    if (!_file) return;

    const uuid = getUniqueId();
    const fileRef = ref(
      storage,
      `Upload/${uuid}/${buildName}${getType(_file)}`
    );

    const fileSize = _file.size;
    const uploadTask = uploadBytesResumable(fileRef, _file);

    uploadTask.on(
      "state_changed",
      (snapshot) => {
        const progress = (snapshot.bytesTransferred / fileSize) * 100;
        setTask(Math.floor(progress));
      },
      (err) => {
        // TODO:    
        console.log("Upload Error : " + err);
      },
      () => {
        // TODO:   
        setUpload(false);
      }
    );

    try {
      await uploadTask;
      // await uploadBytes(fileRef, _file);

      const downloadUrl = await getFileUrl(fileRef);
      setFirestore(uuid, downloadUrl);
    } catch (err) {
      console.log("Error : " + err);
    }
  };

  const setFirestore = async (_dirName, _url) => {
    const docRef = doc(db, "Upload", _dirName);

    // TODO: Document   
    await setDoc(docRef, {
      [`${_dirName}`]: {
        uuid: _dirName,
        buildName: buildName,
        version: version,
        explanation: ex,
        date: new Date(),
        url: _url,
      },
    });
  };

  useEffect(() => {
    if (file && buildName.length > 0 && version.length > 0) {
      setNone(false);
    } else {
      setNone(true);
    }
  }, [buildName, version, file]);

  const handleFileChange = (e) => {
    const _file = e.target.files[0];
    const type = getType(_file);

    switch (type) {
      case ".zip":
      case ".7z": {
        setFile(_file);
        break;
      }
      default: {
        e.target.value = null;
        setFile(null);
        break;
      }
    }
  };

  const onUpload = () => {
    uploadBuild(file);
    setUpload(true);
  };

  const [show, setShow] = useState(false);

  const handleClose = () => setShow(false);
  const handleShow = () => setShow(true);

  useEffect(() => {}, [isUpload]);

  return (
    <>
      <ModalView show={show} handleClose={handleClose} onUpload={onUpload} />
      <div>
        <form className="form-body">
          <div className="fileName-container">
            <span className="input-label">Build Name : </span>
            <input
              className="file-input"
              value={buildName}
              onChange={(e) => {
                setBuildName(e.target.value);
              }}
              type="text"
              placeholder=" "
            />
          </div>
          <div className="version-container">
            <span className="input-label">Version : </span>
            <input
              className="file-input"
              value={version}
              onChange={(e) => {
                setVersion(e.target.value);
              }}
              type="text"
              placeholder=" "
            />
          </div>
          <div className="ex-container">
            <span className="input-label">Explanation : </span>
            <textarea
              value={ex}
              onChange={(e) => {
                setEx(e.target.value);
              }}
              className="ex-input"
              placeholder=" "
            ></textarea>
          </div>
          <div className="file-container">
            <span className="input-label">File : </span>
            <input type="file" accept=".7z,.zip" onChange={handleFileChange} />
          </div>
        </form>
        <div className="btn-area">
          <button
            className={`Upload-Btn ${none ? "Require" : ""}`}
            disabled={none}
            onClick={handleShow}
          >
            Upload
          </button>
        </div>
      </div>
    </>
  );
}

export default Upload;
